:root {
  --primary-color: #002940;
  --secondary-color: #000a0f;
  --tertiary-color: #fff;
  --quaternary-color: #000;
}

body {
  margin: 0;
  background-color: var(--tertiary-color);
  color: var(--quaternary-color);
}

main,
footer {
  padding: 15px;
}

/* ----------------- 
    MAINNAV 
   ----------------- */

/* Toggle button */

.mainnav-toggle {
  display: inline-block;
  padding: 0.75em 15px;
  line-height: 1em;
  font-size: 1.4em;
  color: var(--primary-color);
  width: 100%;
  box-sizing: border-box;

  @media (min-width: 700px) {
    display: none;
  }

  &:focus {
    outline: 0;
  }
}

/* mainnav */

.mainnav {
  position: fixed;
  left: -80vw;
  top: 0;
  height: 100%;
  transition: left 0.3s ease;
  z-index: 20;
  background-color: var(--secondary-color);

  @media (min-width: 700px) {
    position: relative;
    left: auto;
    top: auto;
    height: auto;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 1em 0 0;
    min-height: 100%;
    width: 80vw;
    max-width: 270px;

    @media (min-width: 700px) {
      display: flex;
      padding: 0;
      box-shadow: none;
      background: none;
      white-space: nowrap;
    }

    .dropdown {
      &:hover {
        @media (min-width: 700px) {
          #subnav {
            background-color: var(--primary-color);
            position: absolute;
            top: 40px;
            left: 0;
            box-shadow: 0px 15px 30px 8px rgba(0, 0, 0, 0.45);
            display: flex;
            width: 100%;
            max-width: 700px;
            height: auto;
          }
        }
      }
    }
  }

  a {
    display: block;
    padding: 0.75em 15px;
    line-height: 1em;
    font-size: 1em;
    color: #fff;
    text-decoration: none;
    transition: 0.2s;

    @media (min-width: 700px) {
      color: var(--tertiary-color);
    }

    &:hover {
      opacity: 0.7;
    }
  }

  &:target {
    left: 0;
    outline: 0;
    box-shadow: 3px 0 12px rgba(0, 0, 0, 0.25);

    & + .backdrop {
      position: fixed;
      display: block;
      content: "";
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 15;
      background-color: rgba(0, 0, 0, 0.8);
    }
  }
}

/* Dropdown menu */

#subnav {
  padding: 0 20px;
  overflow: hidden;

  @media (min-width: 700px) {
    display: none;
  }

  ul {
    padding: 0;

    @media (min-width: 700px) {
      flex: 0 0 40%;
      padding: 25px;
      display: block;
    }

    li {
      &:hover {
        @media (min-width: 700px) {
          padding-left: 5px;
        }
      }
    }
  }

  .subnav-image {
    display: none;

    @media (min-width: 700px) {
      flex: 0 0 60%;
      display: block;
    }

    img {
      @media (min-width: 700px) {
        max-width: 450px;
        overflow: hidden;
        margin-bottom: -4px;
      }
    }
  }
}
